import { getComments } from "@/api/article";
export default {
  props: {
    article: {
      type: Object
    }
  },
  data() {
    return {
      comment: []
    }
  },
  async mounted() {
    const res = await getComments(this.article.slug)
    this.comment = res.data.comments
  }
};
